import { FC, useEffect, lazy, Suspense } from "react";
import { Routes, Route } from "react-router-dom"; // 1. react-router-dom 

import { Box, Container, Center, Spinner, HStack } from "@chakra-ui/react";
import AOS from "aos";

import { FloatingChatButton } from "utils/Floating";

import "./App.scss";
import "aos/dist/aos.css";


import { ScrollToTop } from "./utils/ScrollToTop";

// Lazy imports for components
const Navbar = lazy(() => import("shared/navbar/Navbar").then((module) => ({ default: module.Navbar })));
const Footer = lazy(() => import("shared/footer/Footer").then((module) => ({ default: module.Footer })));
const About = lazy(() => import("pages/about/About").then((module) => ({ default: module.About })));
const Home = lazy(() => import("pages/about/Home").then((module) => ({ default: module.Home })));
const OtherProjects = lazy(() => import("pages/other-projects/OtherProjects").then((module) => ({ default: module.OtherProjects })));
const Publication = lazy(() => import("pages/publication/Publication").then((module) => ({ default: module.Publication })));
const ExpThumb = lazy(() => import("pages/experience/ExpThumb").then((module) => ({ default: module.ExpThumb })));
const PageHeader = lazy(() => import("shared/page-header/PageHeader").then((module) => ({ default: module.PageHeader })));
const SidebarProject = lazy(() => import("pages/sidebar/SidebarProject").then((module) => ({ default: module.SidebarProject })));
const SidebarPublication = lazy(() => import("pages/sidebar/SidebarPublication").then((module) => ({ default: module.SidebarPublication })));
const SidebarEtc = lazy(() => import("pages/sidebar/SidebarEtc").then((module) => ({ default: module.SidebarEtc })));




// Loader component for Suspense fallback
const Loader: FC = () => (
    <Center w="100%" h="100vh">
        <Spinner size="xl" color="primary.500" />
    </Center>
);

// 2.      ( )
const PageLayout: FC<{ sidebar: React.ReactNode; children: React.ReactNode }> = ({ sidebar, children }) => (
    <Box mt={{ base: "75px" }}>
        <HStack spacing="-2" justifyContent="space-between" alignItems="flex-start">
            {sidebar}
            <Container maxW="5xl">
                {children}
            </Container>
        </HStack>
    </Box>
);

export const App: FC = () => {
    useEffect(() => {
        AOS.init({ once: true });
    }, []);

    return (
        <Suspense fallback={<Loader />}>
            <Container h="100%" px={{ base: 6, md: 6, lg: 4 }}>
                <Navbar /> {/* Navbar     */}
                <ScrollToTop />

                {/* 3. Routes      */}
                <Routes>
                    {/*  (About)  - path="/" */}
                   
                     
                    <Route
                        path="/"
                        element={
                            <Box mt={{ base: "75px" }}>
                                <Container maxW="5xl">
                                    <About />
                                </Container>
                            </Box>
                        }
                    />

                    <Route
                        path="/chat"
                        element={
                            <Box mt={{ base: "75px" }}>
                                <Container maxW="5xl">
                                    <Home />
                                </Container>
                            </Box>
                        }
                    />

                    {/*   - path="/projects" */}
                    <Route
                        path="/projects"
                        element={
                            <>
                                <PageLayout sidebar={<SidebarProject />}>
                                    <PageHeader id="page-other-projects" label="Projects" />
                                </PageLayout>
                                <Box mt={2}>
                                    <OtherProjects />
                                </Box>
                            </>
                        }
                    />

                    {/*   - path="/publications" */}
                    <Route
                        path="/publications"
                        element={
                            <PageLayout sidebar={<SidebarPublication />}>
                                <PageHeader id="publications" label="Publications" />
                                <Box mt={2}>
                                    <Publication />
                                </Box>
                            </PageLayout>
                        }
                    />
                    
                    {/*    - path="/etc" */}
                    <Route
                        path="/etc"
                        element={
                            <PageLayout sidebar={<SidebarEtc />}>
                                <PageHeader id="page-experience" label="Etc." />
                                <Box mt={2}>
                                    <ExpThumb />
                                </Box>
                            </PageLayout>

                            
                        }
                    />
                </Routes>
                {/* <FloatingChatButton /> */}
                
                <Footer /> {/* Footer     */}
            </Container>
        </Suspense>
    );
};